
import axios from "axios";
import {Toast} from "antd-mobile"
import {history} from "./history";

var token = "";
// axios.defaults.baseURL = "http://localhost:2021/"   // Âü∫Ë∑ØÂæÑ
axios.defaults.baseURL = "http://dengchuang.top:2021/"   // Âü∫Ë∑ØÂæÑ
axios.defaults.headers.common['token'] = token;     // token ‰∏∫ Á©∫
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

//Â∞ÅË£ÖÂØπÂ∫îÁöÑ ÊàêÂäü Â§±Ë¥•ÊèêÁ§∫ Âú®‰∏ãÈù¢Ë∞ÉÁî®
export function loadingToast(msg) {
    Toast.loading(msg, 3, () => {
      console.log('Load complete !!!');
    });
}
export function successToast(msg) {
    Toast.hide();
    Toast.success(msg, 1);
}

export function failToast(msg) {
  Toast.hide();
  Toast.fail(msg, 1);
}

export function offline(msg) {
  Toast.offline(msg, 1);
}



// axios Êã¶Êà™Âô®   Interceptors
// ËØ∑Ê±Ç request Êã¶Êà™Âô®   ËØ∑Ê±Ç‰πãÂâç‰∏öÂä°ÈÄªËæë ÈÖçÁΩÆ data /headers
axios.interceptors.request.use(function (config) {
  // ËØ∑Ê±ÇÂèëÈÄÅ‰πãÂâçÂÅöÁöÑ‰∫ãÊÉÖ
  
  token = sessionStorage.token ?   sessionStorage.token : "";
  config.headers['token']  = token;
  loadingToast("Âä™ÂäõÂä†ËΩΩ...");
  return config;

  
}, function (error) {
  // ËØ∑Ê±ÇÂèëÈÄÅ Â§±Ë¥• 
  offline("ËØ∑Ê±ÇÂ§±Ë¥•---ÁΩëÁªúÈîôËØØ!")
  return Promise.reject(error);
  
});


// ÂìçÂ∫î response Êã¶Êà™Âô®   Ê†πÊçÆËøîÂõûÁöÑÁä∂ÊÄÅÁ†Å ÂÅöÂØπÂ∫îÁöÑ‰∏öÂä°ÈÄªËæë 
axios.interceptors.response.use(function (response) {
    // Ëé∑ÂèñÂà∞ÂìçÂ∫îÊï∞ÊçÆÂÅöÁöÑ‰∫ãÊÉÖ
    // console.log(response)
    setTimeout(()=>{
      successToast(response.data.msg);
      
    },1000);
    if(response.data.code=="400"){
      history.push("/login");
    };
    
    return response;

  }, function (error) {
    failToast("ÊúçÂä°Âô®error-ÂìçÂ∫îÂ§±Ë¥•!!!");
    return Promise.reject(error);
});




export {axios}

